@import '../../styles/variables';
@import '../../styles/mixins';

.heroTitle {
    @include heroFont;

    font-family: $font-titles;
    margin: $offset-basic * 16;
    color: $light;
}

.sectionTitle {
    @include sectionFont;

    font-family: $font-titles;
    margin: $offset-basic * 2;
    text-transform: uppercase;
    color: $light;
}

.subTitle {
    @include subTitleFont;
    @include baseOffset;
    font-family: $font-titles;
    color: $light;
}

.subTitleUpper {
    @extend .subTitle;
    text-transform: uppercase;
}

.subTitleUpper__dark {
    @extend .subTitleUpper;
    color: $dark;
}

.strong {
    font-weight: bolder;
    color: $light;
}

.highlighted {
    color: $orange;
}

.icon_label {
    color: $light;
    font-family: $font-titles;
    font-size: 1.8rem;
    cursor: default;

    &:hover {
        color: $orange;
        transition: color 0.5s linear;
    }
}